}

.catalog__block.woman .catalog__block-img {
  top: -64px;
}

.catalog__block.woman .catalog__block-img > figure {
  margin-top: 151px;
  margin-right: -95px;
}

.catalog__block.woman .catalog__block-img > figure img {
